<template>
  <div class="login">
    <h2>此处可以放跳转的目录便于跟公众号那边链接调试跳转功能</h2>
    <ul>
      <!--<li @click="gotoHome">污染地图</li>-->
      <li @click="gotoCalendar">污染玫瑰</li>
      <!--<li @click="gotoRank">排名</li>-->
      <!--<li @click="gotoActual">实测</li>-->
      <!--<li @click="gotopullutionReport">污染上报</li>-->
      <!--<li @click="gotoSuggest">建议反馈</li>-->
    </ul>
    <div><button @click='emitNowTime'>触发事件</button></div>
    <p>现在的时间：{{nowTime}}</p>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data() {
    return {
      nowTime: 0,
    };
  },
  methods: {
    gotoCalendar() {
      this.$router.push('pollutionRose');
    },
    //    gotoRank() {
    //      this.$router.push('rank')
    //    },
    //    // 首页地图
    //    gotoHome() {
    //      this.$router.push('home')
    //    },
    //    // 实测主页面
    //    gotoActual() {
    //      this.$router.push('actual-measure')
    //    },
    //    // 污染上报
    //    gotopullutionReport() {
    //      this.$router.push('pollutionReport')
    //    },
    //    // 污染反馈
    //    gotoSuggest() {
    //      this.$router.push('suggest-feedback')
    //    }
    reflashTime(nowTime) {
      this.nowTime = nowTime;
    },
    initNowTime() {
      this.$eventBus.$on('now-time-init', this.reflashTime);
    },
    emitNowTime() {
      this.$eventBus.$emit('now-time-init', new Date());
    },
  },
  created() {
    //    console.log('1111',this.$eventBus)
    this.initNowTime();
  },
};
</script>

<style lang="scss" scoped>
  .login {
    ul {
      list-style: none;
      li {
        float: left;
        width: 20%;
        margin-right: 2%;
        height: 140px;
        line-height: 140px;
        background: darkgray;
        color: #000;
        cursor: pointer;
        margin-bottom: 5px;
      }
    }
  }
</style>



