import Vue from 'vue';
import Router from 'vue-router';
// import HelloWorld from '@/components/HelloWorld'
import Login from '@/views/Login';

Vue.use(Router);

const routes = [
  {
    path: '/',
    name: 'demo',
    meta: {
      title: 'demo导航页',
    },
    component: () => import('./demo/demo.vue'),
  },
  {
    path: '/login',
    name: 'login',
    meta: {
      title: '登录页',
    },
    component: Login,
  },
  {
    path: '/pollutionRose',
    name: 'pollutionRose',
    meta: {
      title: '污染玫瑰',
    },
    component: () => import('./views/transmission/pollutionRose/pollutionRose.vue'),
  },
  {
    path: '/example',
    name: 'example',
    meta: {
      title: 'example示例',
    },
    component: () => import('./demo/example/example.vue'),
  },
  {
    path: '/slot',
    name: 'slot',
    meta: {
      title: '插槽-slot',
    },
    component: () => import('./demo/slot/slot.vue'),
  },
  {
    path: '/vuejswar',
    name: 'vuejswar',
    meta: {
      title: 'vue.js实战',
    },
    component: () => import('./demo/vuejswar/vuejswar.vue'),
  },
  {
    path: '/shoppingCart',
    name: 'shoppingCart',
    meta: {
      title: 'vue.js实战-购物车',
    },
    component: () => import('./demo/vuejswar/shoppingCart.vue'),
  },
  {
    path: '/pdf',
    name: 'pdf',
    meta: {
      title: 'pdf预览',
    },
    component: () => import('./demo/pdf/pdf.vue'),
  },
  {
    path: '/drag',
    name: 'drag',
    meta: {
      title: 'drag拖拽',
    },
    component: () => import('./demo/drag/drag.vue'),
  },
  {
    path: '/weather',
    name: 'weather',
    meta: {
      title: '天气预报',
    },
    component: () => import('./demo/weather/weather.vue'),
  },
];

export default new Router({
  mode: 'history',
  routes,
});

export { routes };



