/**
 * 请求方式：POST
 * 使用说明：获取高德天气
 * 使用场景：获取高德天气
 * * */
export const getWeatherInfo = (params, type = "json") => {
  return request({
    url: `${JSON_URL.GET_WEATHER_INFO}`,
    method: "GET",
    data: params,
    type
  });
};



